.DoubleButton {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
}

.DoubleButton-left,
.DoubleButton-right {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: lighter;
    font-family: 'Raleway';
    cursor: pointer;
    border: none;
    border-radius: 5px;
    border: 1px solid #036955;
    transition: transform 0.2s ease, background-color 0.2s ease, color 0.2s ease;
}

.DoubleButton-left {
    background-color: #036955;
    color: white;
}

.DoubleButton-right {
    background-color: #ffffff;
    color: #036955;
}

.DoubleButton-left:hover,
.DoubleButton-right:hover {
    transform: translateY(-2px);
}

.DoubleButton-left:hover {
    background-color: #466FD1;
    color: #fff;
}

.DoubleButton-right:hover {
    background-color: #466FD1;
    color: #fff;
}

.DoubleButton-left:active,
.DoubleButton-right:active {
    transform: translateY(0);
}

@media (max-width: 768px) {
    .DoubleButton {
        flex-direction: column;
        gap: 10px;
    }

    .DoubleButton-left,
    .DoubleButton-right {
        width: 100%;
        text-align: center;
    }
}