.stats-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
  }
  
  .stat-item {
    text-align: center;
  }
  
  .stat-value {
    font-size: 57pt;
    font-weight: 1000;
    color: #2b544a; /* Adjust the color as needed */
    font-family: 'Raleway';
  }
  
  .stat-label {
    font-size: 16pt;
    color: #2b544a; /* Same color as the value */
  }