.goals-dropdown {
  margin-top: 10vh;
  padding: 0 5vw;
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18-column grid */
  grid-column-gap: 1vw; /* Adjust gap if needed */
}

.goals-dropdown h2 {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 1em;
  grid-column: span 18; /* Spanning across all columns */
}

.goals-dropdown p {
  font-size: 18px;
  margin-bottom: 1.5em;
  line-height: 1.6;
  grid-column: span 18;
}

.goal-item {
  margin-bottom: 2vh;
  border-bottom: 1px solid #ccc;
  grid-column: span 18;
}

.goal-title {
  display: flex;
  align-items: center;
  padding: 1.5vh 0;
  cursor: pointer;
  font-weight: bold;
  font-size: 1.25em;
  grid-column: span 18;
}

.chevron-icon {
  width: 16px;
  height: 16px;
  margin-right: 2vw;
}

.goal-content {
  margin-top: 7.5vh;
  margin-bottom: 15vh;
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18-column grid */
  grid-column-gap: 1vw; /* Adjust gap */
  grid-template-rows: auto; /* Allow rows to expand based on content */
  grid-column: span 18;
  align-items: flex-start;
}

.goal-content-text {
  grid-column: 7 / span 11; /* Start text at column 2 and span 11 columns */
  grid-row: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1em;
}

.goal-content-text p {
  font-size: 18px;
  line-height: 1.6;
  margin: 0 0 1em 0;
  text-align: left;
}

.goal-item .image-section {
  grid-column: 2 / span 4; /* Image section spans 6 columns */
  grid-row: 1; /* Set the image section on the first row */
  display: grid;
  grid-template-rows: auto; /* Define two rows: one for the image, one for the vector */
  margin-top: -5vh;
}

.goal-item .image-section .image-wrapper {
  grid-row: 1; /* Image on the first row */
  z-index: 2; /* Ensure the image is above the vector */
}

.goal-item .image-section .image-wrapper img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.goal-item .background-vector {
  grid-row: 1; /* Vector on the second row */
  z-index: 1; /* Ensure the vector is below the image */
  width: 85%; /* Make the vector take up full width */
}

.goal-item .image-section .partner-caption {
  width: 90%;
  background-color: #466FD1;
  color: #fff;
  grid-row: 3; /* Place caption after the vector */
  z-index: 2;
}