.our-refuge__content-grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 40px; /* Spacing between the text and image */
    align-items: center; /* Aligns the image and text vertically in the middle */
  }
  
  .our-refuge__text-block {
    grid-column: 2 / span 6; /* Text spans 7 out of 12 columns */
    display: flex;
    flex-direction: column;
    justify-content: center; /* Vertically center the text */
    padding-right: 20px; /* Add some space between the text and the image */
    margin-left: -1vw;
  }
  
  .our-refuge__text-block ul {
    margin-top: 10px; /* Reduce space above the list */
    padding-left: 20px; /* Proper indentation for bullet points */
    list-style-type: disc; /* Standard bullet point */
  }
  
  .our-refuge__text-block li {
    margin-bottom: 5px; /* Reduce the space between bullets */
    line-height: 1.4; /* Adjust line height for compactness */
  }
  
  .our-refuge__image-block {
    grid-column: span 5; /* Image spans 5 out of 12 columns */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .our-refuge__accommodation-image {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

  .reversed {
    display: grid;
    grid-template-columns: repeat(12, 1fr); /* Still use 12 columns */
    grid-gap: 40px;
    align-items: center;
  }
  
  .reversed .our-refuge__text-block {
    grid-column: span 7; /* Text still takes up 7 columns */
    order: 2; /* Reverse the order for text */
  }
  
  .reversed .our-refuge__image-block {
    grid-column: span 5; /* Image still takes up 5 columns */
    order: 1; /* Reverse the order for image */
  }

  .reverser .partner-caption {
    background-color: #466FD1;
  }

  .refuge_photos {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap; /* Allow the photos to wrap on smaller screens */
    padding: 20px 0; /* Add some padding to the section */
  }
  
  .PhotoLine_photoItem__aU6D6{
    width: 21.1vw; /* Scale the images to be 25% of the container width minus the gap */
    height: auto;
    border-radius: 10px;
    object-fit: cover; /* Ensure the image covers the box while maintaining aspect ratio */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional shadow for better aesthetics */
    margin-top: 5vh;
    margin-bottom: -5vw;
}

.vicky_button {
    background-color: white;
    color: #036955;
    border: 2px solid #036955;
    text-align: center;
}

.vicky_button:hover {
    background-color: #466FD1;
    border:#466FD1;
    color: white;
  }

.vicky_button_container {
    display: flex;
    justify-content: center; /* Centers horizontally */
    margin-left: -7vw;
    margin-top: -5vh;
    margin-bottom: 5vw;
  }
  
  @media (max-width: 768px) {
    .refuge_photos img {
      width: calc(50% - 20px); /* Scale the images to 50% of the container width on tablet screens */
    }
  }
  
  @media (max-width: 480px) {
    .refuge_photos img {
      width: calc(100% - 20px); /* Scale the images to full width on mobile screens */
    }
  }