/* General grid layout for sections */
.text-block-container {
    display: grid;
    grid-template-columns: repeat(18, 1fr); /* 18-column layout */
    grid-auto-rows: auto; /* Automatically adjust row heights */
    grid-gap: 20px; /* Space between grid items */
    padding: 40px;
    font-family: 'Raleway';
}

/* Hero text block styling */
.text-block-hero, .text-block-donation-money, .text-block-donation-items, .text-block-items-list {
    grid-column: 1 / span 18; /* Span full width for the hero section */
    margin: 0 0 30px;
    font-size: 16pt;
    line-height: 1.6;
    color: #555;
    margin-left: 5.2vw;
    margin-right: 4vw;
    font-family: 'Raleway';
}

/* Section headings and text styling */
.donation-money h2, .donation-items h2 {
    font-size: 1.8rem; /* Heading size */
    font-weight: bold;
    margin-bottom: 20px; /* Space below heading */
    color: #333;
}

.donation-money p, .donation-items p {
    font-size: 16px; /* Paragraph text size */
    line-height: 1.6;
    color: #555;
}

/* Placeholder for image or video */
.donation-placeholder {
    grid-column: 2 / span 16; /* Full width placeholder */
    background-color: #e0e0e0; /* Light gray background for the placeholder */
    height: 400px; /* Placeholder height */
    margin: 30px 0; /* Space around the placeholder */
}

/* Donation photos section - aligning with the 18-column grid */
.donation-photos {
    display: grid;
    grid-template-columns: repeat(18, 1fr); /* Same 18-column layout */
    grid-gap: 20px;
    grid-column: 1 / span 18; /* Full width */
    justify-content: center; /* Center content horizontally */
    margin-bottom: 10vh;
    margin-left: -4.3vw;
}

/* Photo line within the grid */
.PhotoLine_photoLine__Aa3yT{
    grid-column: 2 / span 18; /* Full width for photo line */
    display: flex; /* Use flexbox to center images */
    justify-content: center; /* Center images */
    flex-wrap: wrap; /* Wrap images if they exceed available space */
    grid-gap: 2vw;
    margin-bottom: 2vh;
    margin-left: -1.8vw;
}

/* Images in the photo line */
.donation-photos img {
    flex: 0 1 calc(22% - 20px); /* Each image takes up 30% of the row, minus the gap */
    max-width: 100%; /* Ensure images are responsive */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-radius: 8px;
}

/* Add hover effect */
.donation-photos img:hover {
    transform: scale(1.1); /* Slightly scale the image on hover */
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

/* Charity shop section styling */
.text-block-charity-shop {
    grid-column: 1 / span 18; /* Span full width for the hero section */
    font-size: 16pt;
    line-height: 1.6;
    color: #555;
    font-family: 'Raleway';
}
.charity-shop-h2 h2 {
    grid-column: 2 / span 12;
    margin-left: 7.5vw; /* Adjust margin */
    margin-bottom: 0; /* Remove negative margin */
}

#root > main > section:nth-child(5) > div.charity-shop-p > section > div.ImageRightTextLeft-content {
    margin-left: -6.7vw; /* Adjust margin */
    margin-bottom: 0; /* Remove negative margin */
}

/* More ways to get involved section */
#root > main > section.captioned-photo-section > h2 {
    grid-column: 2 / span 18; /* Span full width for the hero section */
    margin: 0 0 30px;
    font-size: 24pt;
    line-height: 1.6;
    color: #555;
    margin-left: 2vw;
    margin-right: 4vw;
    font-family: 'Raleway';
}

.captioned-photo-section {
    display: grid;
    grid-template-columns: repeat(18, 1fr); /* 18-column layout */
    grid-auto-rows: auto;
    margin-top: 40px;
    justify-content: center;
}

.captioned-photo-container {
    grid-column: 1 / span 18;
    display: grid;
    grid-template-columns: repeat(6, 1fr); /* Adjust based on the number of photos */
    grid-gap: 2vw;
    margin-top: 2vw;
    margin-left: 7.5vw;
}

.caption-photo-link {
    display: inline-block;
    text-decoration: none;
    width: 100%;
}

.caption-photo-link > div {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-radius: 8px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transform: scale(0.75);
}

.caption-photo-link > div:hover {
    transform: scale(0.85);
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

.caption-photo-link > div > img {
    width: 100%;
    display: block;
    border-radius: inherit;
}

#root > main > section:nth-child(5) > section > div.ImageLeftTextRight-image {
    margin-left: 5.8vw;
}

.items-list-container {
    display: flex; /* Use flexbox to make items render horizontally */
    justify-content: space-between; /* Distribute space between the two lists */
    margin-top: 40px;
    width: 93%;
    margin-left: 2vw;
}

.items-list-container .items-list {
    flex: 1; /* Each list takes equal space */
}

@media (max-width: 1024px) {
    .items-list-container {
        grid-template-columns: 1fr; /* Stack the lists vertically on smaller screens */
    }

    .text-block-container {
        grid-template-columns: repeat(9, 1fr); /* 9-column layout for medium screens */
    }

    .donation-photos {
        grid-template-columns: repeat(9, 1fr); /* Adjust to 9 columns */
    }

    .photo-line {
        flex-wrap: wrap; /* Allow wrapping on smaller screens */
        flex: 0 1 calc(40% - 20px); /* Increase the image size on medium screens */
    }
}

@media (max-width: 768px) {
    .text-block-container {
        grid-template-columns: repeat(6, 1fr); /* Single column for mobile */
    }

    .donation-photos {
        grid-template-columns: repeat(6, 1fr); /* Adjust to 6 columns for smaller screens */
    }

    .photo-line {
        flex-wrap: wrap; /* Wrap images */
        flex: 0 1 calc(50% - 20px); /* Increase image size for mobile */
    }
}