.photoLine {
  display: flex;
  flex-wrap: wrap; /* Ensure images wrap into multiple rows if necessary */
  justify-content: center; /* Center the images */
  gap: 20px; /* Add space between images */
}

.photoItem {
  width: 300px; /* Set a fixed width */
  height: 200px; /* Set a fixed height */
  border-radius: 5px; /* Add rounded corners if needed */
  object-fit: cover; /* Ensures the image fills the container without distortion */
  transition: transform 0.2s ease-in-out;
}

.photoItem:hover {
  transform: scale(1.05); /* Optional: Add a zoom effect on hover */
}

@media (max-width: 768px) {
  .photoItem {
    width: 200px; /* Adjust the size for tablet-sized screens */
    height: 150px;
  }
}

@media (max-width: 480px) {
  .photoItem {
    width: 150px; /* Adjust the size for mobile screens */
    height: 100px;
  }
}