/* Import the Raleway font */
@font-face {
  font-family: 'Raleway';
  src: url('../../../assets/Fonts/Raleway/static/Raleway-Regular.ttf') format('truetype'); /* Update the path if necessary */
  font-weight: normal;
  font-style: normal;
}

.hero {
  width: 100%;
  padding: 80px 20px; /* Adjust padding as needed */
  background-color: #ffffff; /* Fallback background color */
  text-align: center;
  font-family: 'Raleway', sans-serif; /* Apply the Raleway font */
  background-size: cover;
  background-position: center;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 2.5rem; /* Adjust font size as needed */
  color: #036955; /* Color matching the logo and theme */
  margin-bottom: 20px;
  font-weight: bold;
}

.hero p {
  font-size: 1.25rem; /* Adjust font size as needed */
  color: #333; /* Dark gray text color */
}