.logo-scroller-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 20px 0;
  background-color: #fff;
  display: flex;
  align-items: center; /* Vertically center chevrons and logos */
  justify-content: space-between; /* Space chevrons and logos */
}

.logo-scroller {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: scroll;
  scroll-behavior: smooth;
  white-space: nowrap; /* Prevent items from wrapping */
  flex: 1; /* Ensure the logo scroller takes the remaining space between chevrons */
  padding: 0 20px; /* Optional: Add some padding inside the scrolling area */
  -webkit-overflow-scrolling: touch;
}

.logo-track {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.logo-item {
  flex: 0 0 auto;
  width: 40vh; /* Adjust size as needed */
  margin-right: 20px; /* Optional: add spacing between logos */
}

.logo-item img {
  max-width: 100%;
  height: auto;
}

/* Chevron button styles */
.scroll-btn {
  background: none;
  border: none;
  cursor: pointer;
  z-index: 10;
  padding: 0 10px;
}

.scroll-btn img {
  width: 40px; /* Adjust size of chevron */
  height: 40px;
}

.scroll-btn.left {
  margin-right: 10px;
}

.scroll-btn.right {
  margin-left: 10px;
}

/* Optional: Hide scrollbars */
.logo-scroller::-webkit-scrollbar {
  display: none;
}