/* Base Grid Setup */
.container {
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18 equal columns */
  width: 100%;
  position: relative;
}

/* Navbar Header */
.navbar-header {
  background-color: #f4f4f4;
  transition: top 0.3s ease-in-out; /* Smooth transition */
  top: 50px; /* Ensure the navbar is positioned below the top bar */
  position: sticky;
  z-index: 500;
  grid-column: span 18;
}

/* Hidden Navbar */
.hidden {
  top: -100px; /* Move navbar out of view */
}

/* Scrolled Up Navbar */
.scrolled-up {
  top: 35px; /* Ensure the navbar is visible */
}

/* Navbar Main Section */
.navbar {
  background-color: white;
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* Create an 18-column grid for the navbar */
  align-items: center;
  padding: 15px 25px;
  font-family: Arial, Helvetica, sans-serif;
  position: sticky;
  top: 50px; /* Ensure it sits below the top bar */
  z-index: 999;
  transition: top 0.3s ease-in-out; /* Transition for smooth appearance */
  margin-top: 2vw;
}

.navbar::after {
  content: '';
  display: block;
  width: 85%; /* Adjust this value to control the width of the border */
  margin: 0 auto; /* Center the border */
  border-bottom: 2px solid #036955; /* Add the bottom border */
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
}

/* Navbar Logo */
.navbar-logo {
  height: 100px;
  grid-column: 5 / 7; /* Span across columns 2 to 5 */
}

/* Navbar Menu */
.navbar-menu {
  list-style: none;
  display: flex;
  gap: 40px;
  padding: 0;
  grid-column: 6 / 15; /* Span across columns 6 to 15 */
  justify-content: center;
}

/* Navbar Items */
.navbar-item {
  position: relative;
}

.navbar-item a {
  color: black;
  text-decoration: none;
  font-size: 1rem;
  display: flex;
  align-items: center;
  transition: color 0.3s; /* Add transition for smooth hover effect */
}

.navbar-item a:hover {
  color: #466FD1; /* Change color on hover */
  font-weight: bolder;
}

/* Dropdown Arrow */
.dropdown-arrow {
  margin-left: 5px;
  width: 10px; /* Adjust the size as needed */
  height: auto; /* Maintain aspect ratio */
  fill: currentColor; /* Inherit the text color */
  transition: transform 0.3s; /* Add transition for smooth hover effect */
}

.dropdown-arrow.active {
  transform: rotate(180deg); /* Rotate the arrow when active */
}

/* Dropdown Menu */
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 50%; /* Center horizontally relative to the parent */
  transform: translateX(-50%); /* Center horizontally relative to the parent */
  background-color: white;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  padding: 10px 0;
  z-index: 1000;
  min-width: 200px; /* Set minimum width */
  width: auto; /* Adjust width as needed */
}

.dropdown-menu::before {
  content: '';
  display: block;
  height: 4px; /* Adjust the height of the blue bar */
  background-color: #466FD1; /* Blue color */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.navbar-item.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.dropdown-menu a {
  display: block;
  padding: 10px 20px;
  color: black;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.dropdown-menu a:hover {
  background-color: #f4f4f4;
  color: #466FD1; /* Change color on hover */
  font-weight: bolder;
}

/* Navbar Left and Right Sections */
.navbar-left {
  grid-column: 6 / 2; /* Positioned at the far left */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: -3.5vw;
}

.navbar-right {
  grid-column: 15 / 17; /* Positioned at the far right */
  display: flex;
  justify-content: center;
  align-items: left;
}

/* Get Help Button */
.get-help-button {
  grid-column: 16 / 18; /* Positioned between columns 16 and 18 */
  display: flex;
  justify-content: center;
}

.get-help-button button {
  background-color: #004d40;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px; /* Adjust this value to round the corners */
}

.get-help-button button:hover {
  background-color: #466FD1;
  color: #fff;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .navbar {
    grid-template-columns: 1fr; /* Collapse to a single column */
    flex-direction: column; /* Stack items vertically on smaller screens */
    align-items: center; /* Center align items */
  }

  .navbar-logo,
  .get-help-button {
    margin: 10px 0; /* Add spacing on smaller screens */
  }

  .navbar-menu {
    flex-direction: column; /* Stack menu items vertically on smaller screens */
    gap: 10px;
    margin-right: 0;
    justify-content: center;
  }

  .navbar-left,
  .navbar-right {
    grid-column: span 18; /* Expand the sections on smaller screens */
    justify-content: center;
  }

  .get-help-button {
    margin-top: 10px;
    z-index: 10;
  }
}