/* Base Grid Setup */
.container {
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18 equal columns */
  width: 100%;
}

.navbar-top {
  background-color: #466FD1;
  color: white;
  padding: 10px 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
  grid-column: span 18;
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* Reapply grid within navbar */
  align-items: center;
}

.contact-info {
  display: flex;
  grid-column: 3 / span 8; /* Adjust to the space you need */
  gap: 20px;
  align-items: center;
}

.contact-info span {
  position: relative;
  color: white;
}

.contact-info a {
  color: white;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.contact-info span::after {
  content: '';
  height: 100%;
  border-left: 1px solid white;
  margin-left: 10px;
  position: absolute;
  right: -10px;
  top: 0;
}

.contact-info span:last-child::after {
  display: none;
}

.donate-button-container {
  display: flex;
  align-items: center;
  grid-column: 15 / span 1; /* Adjust the columns accordingly */
  margin-left: 10vw;
}

.donate-button {
  position: relative;
}

.donate-button-topbar button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  padding: 0 10px;
  border-radius: 5px;
  font-family: 'Raleway', sans-serif;
  text-decoration: none;
}

.donate-button-topbar button:hover {
  text-decoration: underline;
}

.donate-button-topbar::before,
.donate-button-topbar::after {
  content: '';
  height: 100%;
  border-left: 1px solid white;
  position: absolute;
  top: 0;
}

.donate-button-topbar::before {
  left: -5px;
}

.donate-button-topbar::after {
  right: -5px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar-top {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .contact-info {
    grid-column: span 18;
    justify-content: center;
    margin-bottom: 10px;
  }

  .donate-button-container {
    grid-column: span 18;
    justify-content: center;
  }
}