:root {
  --grid-gap: 20px;
  --small-grid-gap: 10px;
  --padding: 20px;
  --small-padding: 10px;
  --font-size: 16pt;
  --font-family: 'Raleway', sans-serif;
  --border-radius: 15px;
  --bg-color-caption: #f4e04d;
}

/* Base Styles */
.partner-section-wrapper {
  display: grid;
  grid-template-columns: repeat(18, 1fr);
  gap: var(--grid-gap);
  padding: var(--padding);
  position: relative;
  z-index: 1;
}

.text-block-our-partners {
  grid-column: 2 / span 3;
  font-family: var(--font-family);
  font-size: var(--font-size);
  margin-left: 0.9vw;
}

.logo-scroller {
  grid-column: span 18;
  margin-top: var(--grid-gap);
  position: relative;
  z-index: 2;
}

.partner-cta-section {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
  align-items: center;
  padding: var(--padding);
  grid-column: span 12;
}

.partner-cta-section .text-section {
  grid-column: 2 / span 5;
  text-align: left;
  margin-left: -2.3vw;
  padding: var(--small-padding);
}

.partner-cta-section .image-section {
  grid-column: 8 / span 4;
  justify-self: center;
  margin-top: 2vh;
}

.partner-cta-section .image-section img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.partner-cta-section .image-section .image-wrapper {
  display: inline-block;
  padding: var(--small-padding);
}

.partner-caption {
  display: inline-block;
  width: 91.7%;
  background-color: var(--bg-color-caption);
  text-align: center;
  border-radius: 0 0 var(--border-radius) var(--border-radius);
}

/* Medium Screens (Tablets) */
@media (max-width: 1024px) {
  .partner-section-wrapper {
    grid-template-columns: repeat(12, 1fr);
  }

  .text-block-our-partners {
    grid-column: 1 / span 4;
    margin-left: 0;
  }

  .logo-scroller {
    grid-column: span 12;
  }

  .partner-cta-section {
    grid-template-columns: repeat(12, 1fr);
    grid-column: span 12;
  }

  .partner-cta-section .text-section {
    grid-column: 1 / span 6;
    margin-left: 0;
  }

  .partner-cta-section .image-section {
    grid-column: 7 / span 6;
  }
}

/* Small Screens (Landscape Phones) */
@media (max-width: 768px) {
  .partner-section-wrapper {
    grid-template-columns: 1fr;
    gap: var(--small-grid-gap);
  }

  .text-block-our-partners,
  .logo-scroller,
  .partner-cta-section {
    grid-column: span 1;
    margin-left: 0;
  }

  .partner-cta-section {
    grid-template-columns: 1fr;
    padding: var(--small-padding);
  }

  .partner-cta-section .text-section,
  .partner-cta-section .image-section {
    grid-column: span 1;
    padding: var(--small-padding) 0;
    margin-left: 0;
  }

  .partner-cta-section .image-section {
    margin-top: var(--small-padding);
  }
}

/* Extra Small Screens (Portrait Phones) */
@media (max-width: 480px) {
  .text-block-our-partners {
    font-size: 14pt;
  }

  .partner-caption {
    width: 100%;
  }

  .partner-cta-section .text-section {
    font-size: 14pt;
  }
}