.partner-quote-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #ffff;
    text-align: center;
    margin: 20px auto;
    max-width: 800px;
}

.vector-wrapper {
    position: relative;
    margin-bottom: 20px;
}

.partner-quote-vector {
    width: 100px; /* Adjust the size as needed */
    height: auto;
    margin-top: 20vh;
}

.partner-quote-content {
    font-family: 'Raleway', sans-serif;
}

.partner-quote-text {
    font-size: 20pt; /* Adjust the font size as needed */
    color: #004d40;
    margin-bottom: 20px;
}

.partner-quote-author {
    font-size: 20pt; /* Adjust the font size as needed */
    color: #004d40;
}

.partner-quote-company {
    font-weight: bold;
}