.ImageLeftTextRight-section {
    display: flex;
    align-items: center;
    padding: 40px 20px;
    background-color: #fff;
  }
  
  .ImageLeftTextRight-image {
    flex: 1.5;
    padding: 10px;
    margin-left: 12.5vw;
    
  }
  
  .ImageLeftTextRight-image img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Enhanced shadow */
  }
  
  .ImageLeftTextRight-content {
    flex: 2;
    padding: 20px;
  }
  
  .ImageLeftTextRight-content p {
    font-size: 16pt; /* Adjust font size as needed */
    color: #333; /* Dark gray text color */
    line-height: 1.5;
    margin-bottom: 20px;
    margin-left: 2vw;
    margin-right: 12vw;
    text-align: left;
  }
  
  .ImageLeftTextRight-buttons {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  .ImageLeftTextRight-button {
    padding: 10px 20px;
    font-size: 1rem;
    font-family: 'Raleway', sans-serif; /* Use Raleway font */
    cursor: pointer;
    border-radius: 5px;
    border: none;
    transition: background-color 0.3s ease;
  }

  .ImageLeftTextRight-buttons {
    margin-top: 20px; /* Add some space above the buttons */
    display: flex;
    gap: 15px; /* Space between the buttons */
    justify-content: flex-start;
    margin-left: 2vw; /* Align buttons to the start (left) */
  }
  
  .get-help {
    background-color: #004d40;
    color: white;
    margin-left: 2vw;
    margin-right: 2vw;
  }
  
  .get-help:hover {
    background-color: #00332b;
  }
  
  .donate {
    background-color: transparent;
    color: #004d40;
    border: 2px solid #004d40;
  }
  
  .donate:hover {
    background-color: #004d40;
    color: white;
  }

  /* Global Typography Styles */
body {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

h1 {
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: bold;
}

h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 500; /* Medium weight */
}

h3 {
  font-family: 'Helvetica', sans-serif;
  font-size: 20px;
  font-weight: bold;
}

p {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px;
  font-weight: 400;
}

ol {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

ul {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

.subtext {
  font-family: 'Helvetica', sans-serif;
  font-size: 12px;
  font-weight: 300; /* Light */
}