.map-section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh; /* Adjust the height as needed */
  }
  
  .map-image {
    width: 45vw;
    height: auto;
    align-self: center; /* Ensure the image is centered */
    margin-left: 5vw;
    margin-top: 20vh;
    border-radius: 5%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1);
  }
  
  .map-content {
    width: 50%;
    text-align: left;
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: 18vh;
  }
  
  .map-content h2 {
    font-family: 'Raleway', sans-serif;
    font-size: 24pt;
    margin-bottom: 10px;
  }
  
  .map-content p {
    font-size: 16pt;
    margin-bottom: 20px;
  }
  
  .map-buttons {
    display: flex;
    gap: 10px;
  }
  
  .donate-button {
    background-color: #004d40;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    font-family: 'Raleway', sans-serif;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .volunteer-button {
    background-color: #fff;
    color: #004d40;
    border: 2px solid #004d40;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    font-family: 'Raleway', sans-serif;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .donate-button:hover {
    background-color: #466FD1;
  }
  
  .volunteer-button:hover {
    background-color: #466FD1;
    color: white;
  }