/* Global Typography Styles */
body {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

h1 {
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: bold;
}

h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 500; /* Medium weight */
}

h3 {
  font-family: 'Helvetica', sans-serif;
  font-size: 20px;
  font-weight: bold;
}

p {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px;
  font-weight: 400;
}

ol {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

ul {
  font-family: 'Helvetica', sans-serif;
  font-size: 16px; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

.subtext {
  font-family: 'Helvetica', sans-serif;
  font-size: 12px;
  font-weight: 300; /* Light */
}