.safety-exit-button {
    position: fixed;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #466FD1;
    color: white;
    border: none;
    padding: 15px; /* Increased padding-right */
    cursor: pointer;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 5px 0 0 5px; /* Rounded left corners */
    z-index: 1000; /* Ensure it stays above other elements */
    writing-mode:vertical-lr; /* Rotate text for vertical reading */
    text-orientation: mixed;
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  
  .safety-exit-icon {
    margin-top: 10px; /* Space between the text and the icon */
    margin-bottom: 10px;
    width: 16px; /* Adjust size as needed */
    height: 16px; /* Adjust size as needed */
    fill: white; /* Ensure the icon color matches the text color */
  }