.text-block-fundraising-hero {
    font-family: 'Raleway';
    text-align: left;
    font-size: 16pt;
    margin-left: 7.4vw;
    margin-right: 7vw;
}

.text-block-fundraising-ideas {
    font-family: 'Raleway';
    text-align: left;
    font-size: 16pt;
    padding-left: 7.3vw;
}

.text-block-undefined {
    font-family: 'Raleway';
    text-align: left;
    font-size: 16pt;
    margin-left: 7.3vw;
    margin-right: 7.3vw;
}

.partner-button, .image-text-button {
    margin-left: 7.3vw; /* Align buttons with the text */
}

.text-block-grant-funders {
    font-family: 'Raleway';
    text-align: left;
    font-size: 16pt;
    padding-left: 7.3vw;  
}

@media (max-width: 1024px) {
    .text-block-fundraising-hero,
    .text-block-fundraising-ideas,
    .text-block-undefined,
    .text-block-grant-funders {
        font-size: 14pt; /* Adjust font size for medium screens */
        padding-left: 5vw; /* Reduce padding on medium screens */
    }

    .partner-button, .image-text-button {
        margin-left: 0vw; /* Adjust margin for buttons on medium screens */
    }
}

#root > div > section:nth-child(9) > div.ImageLeftTextRight-content > div > button {
    margin-left: 0vw;
}

#root > div > section:nth-child(5) > div.ImageLeftTextRight-content > div > button {
    margin-left: 0vw;
}

#root > div > section.ImageRightTextLeft-section > div.ImageRightTextLeft-content > p {
    margin-left: 5.2vw;
}

#root > div > section.ImageRightTextLeft-section > div.ImageRightTextLeft-content > div > button {
    margin-left: -6.7vw;
}

/* Global Typography Styles */
body {
    font-family: 'Helvetica', sans-serif;
    font-size: 16pt; /* Base font size for body */
    line-height: 1.5;
    color: #333;
  }
  
  h1 {
    font-family: 'Raleway', sans-serif;
    font-size: 30pt;
    font-weight: bold;
  }
  
  h2 {
    font-family: 'Raleway', sans-serif;
    font-size: 24pt;
    font-weight: 500; /* Medium weight */
  }
  
  h3 {
    font-family: 'Helvetica', sans-serif;
    font-size: 20pt;
    font-weight: bold;
  }
  
  p {
    font-family: 'Helvetica', sans-serif;
    font-size: 16pt;
    font-weight: 400;
  }
  
  ol {
    font-family: 'Helvetica', sans-serif;
    font-size: 16pt; /* Base font size for body */
    line-height: 1.5;
    color: #333;
  }
  
  ul {
    font-family: 'Helvetica', sans-serif;
    font-size: 16pt; /* Base font size for body */
    line-height: 1.5;
    color: #333;
  }
  
  .subtext {
    font-family: 'Helvetica', sans-serif;
    font-size: 12pt;
    font-weight: 300; /* Light */
  }
@media (max-width: 768px) {
    .text-block-fundraising-hero,
    .text-block-fundraising-ideas,
    .text-block-undefined,
    .text-block-grant-funders {
        font-size: 12pt; /* Further adjust font size for mobile screens */
        padding-left: 3vw; /* Further reduce padding on mobile screens */
        text-align: center; /* Center align text on mobile for better readability */
    }

    .partner-button, .image-text-button {
        margin-left: 3vw; /* Adjust margin for buttons on mobile screens */
        text-align: center; /* Center buttons on mobile screens */
    }
}