/* General Page Structure */
.our-history-page {
    background-color: #ffff;
    font-family: 'Raleway', sans-serif;
}

/* History Content Section - Mirrored Structure */
.history-content-section {
    display: grid;
    grid-template-columns: repeat(18, 1fr); /* 18-column grid */
    gap: 20px;
    padding: 20px;
    align-items: center; /* Center the content vertically */
    position: relative;
    height: auto;
}

#root > div > div.history-content-section > div.image-section > div {
    transform: scale(1.5);
}
#root > div > div.history-content-section > div.image-section > div > div {
    width: 120%;  /* Ensures the caption takes the full width of the image */
    text-align: center;  /* Center align the text */
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
}

/* Image Section (VectorImageCaption) in History Section */
.history-content-section .image-section {
    grid-column: 10 / span 9; /* Image section takes up 9 columns */
    display: grid;
    justify-items: center; /* Center image and caption */
    align-items: center;
    position: relative;
    z-index: 2; /* Ensure the image and caption are above the vector */
    margin-right: 3vw;
}

.history-content-section .image-section .staff-photo {
    border-radius: 10px;
    width: 100%;
    height: auto;
    z-index: 2; /* Ensure the image appears above the vector */
}

.history-content-section .image-section .background-vector {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.85); /* Slightly reduce the size of the vector */
    width: 100%;
    height: auto;
    z-index: 0; /* Ensure the vector is behind the image and caption */
    pointer-events: none;
}

.history-content-section .image-section .partner-caption {
    background-color: #A4EFE1; /* Light blue background */
    color: #333333; /* Dark text */
    padding: 10px 20px;
    border-radius: 5px;
    text-align: center;
    z-index: 2; /* Ensure the caption is above the vector */
    width: 100%; /* Ensure the caption spans the width of the image */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 2.5vh;
    max-width: 88.5%; /* Ensure it doesn't exceed the container */
    margin-left: -0.47vw;
}

/* TextLeft Component in History Section */
.history-content-section .text-section {
    grid-column: 1 / span 9; /* Text section takes up 9 columns */
    padding: 0 5vw;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center the text vertically */
    align-items: flex-start; /* Align text to the left */
    text-align: left;
    height: 100%;
    margin-left: 1.4vw;
}

/* Logo Content Section */
.logo-content-section {
    display: grid;
    grid-template-columns: repeat(18, 1fr); /* 18-column grid */
    gap: 20px;
    padding: 20px;
    align-items: center; /* Center the content vertically */
    position: relative;
    height: auto;
}

/* TextLeft Component in Logo Section */
.logo-content-section .text-section {
    grid-column: 8 / span 12; /* Text section takes up 9 columns */
    grid-row: 1;
    padding: 0 5vw;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center the text vertically */
    align-items: flex-start; /* Align text to the left */
    text-align: left;
    height: 100%;
    margin-left: -2vw;
}

/* Image Section (VectorImageCaption) in Logo Section */
.logo-content-section .image-section {
    grid-column: 3 / span 3; /* Image section takes up 9 columns */
    display: grid;
    justify-items: center; /* Center image and caption */
    align-items: center;
    position: relative;
    z-index: 2; /* Ensure the image and caption are above the vector */
    transform: scale(1.3);
    margin-left: 1.7vw;
    margin-top: 5vh;
}

.logo-content-section .image-section .staff-photo {
    border-radius: 10px;
    width: 100%;
    height: auto;
    z-index: 2; /* Ensure the image appears above the vector */
    margin-bottom: 3vh;
}

.logo-content-section .image-section .background-vector {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.85); /* Slightly reduce the size of the vector */
    width: 100%;
    height: auto;
    z-index: 0; /* Ensure the vector is behind the image and caption */
    pointer-events: none;
}

.logo-content-section .image-section .partner-caption {
    background-color: #036955; /* Dark green background */
    color: #ffffff; /* White text */
    padding: 10px 20px;
    border-radius: 5px;
    text-align: center;
    z-index: 2; /* Ensure the caption is above the vector */
    width: 17.5vw; /* Make the caption wider than the image */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Photo Section */
.photo-content-section {
    display: flex;
    justify-content: center;
    padding: 20px;
    margin-top: 5vh;
    margin-bottom: 13vh;
    margin-left: 6vw;
}

.PhotoLine_photoLine__Aa3yT{
    grid-column: 2 / span 18; /* Full width for photo line */
    display: flex; /* Use flexbox to center images */
    justify-content: center; /* Center images */
    flex-wrap: wrap; /* Wrap images if they exceed available space */
    grid-gap: 1.2vw;
    margin-bottom: 20px;
    margin-left: -1.8vw;
}

.photo-item {
    width: 200px; /* Adjusted to a larger size based on screenshot */
    height: auto;
    border-radius: 15px; /* Rounded corners */
    object-fit: cover; /* Ensure the image covers the entire box */
    position: relative;
}
.photo-item img:hover {
    transform: scale(1.1); /* Slightly scale the image on hover */
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}


.values-content-section {
    margin-top: 10vh;
    padding: 0 5vw;
    text-align: left;
    margin-left: 2.3vw;
}

.values-content-section h2 {
    font-size: 2em;
    font-weight: lighter;
    margin-bottom: 1em;
}

.values-content-section p {
    font-size: 18px;
    margin-bottom: 1.5em;
    line-height: 1.6;
}

.values-content-section ul {
    list-style-type: disc;
    padding-left: 20px;
    margin-bottom: 1.5em;
}

.values-content-section ul li {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 1em;
}

#root > div > div.goals-dropdown {
    margin-left: 2.2vw;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
    .founder-image-section,
    .history-content-section,
    .logo-content-section {
        grid-template-columns: 1fr; /* Stack sections vertically */
    }

    .history-content-section .text-section,
    .history-content-section .image-section,
    .logo-content-section .image-section {
        grid-column: span 18; /* Full width for mobile */
    }

    .background-vector {
        transform: translate(-50%, -50%) scale(0.75); /* Slight reduction for small screens */
    }

    .partner-caption {
        margin-top: 20px;
        max-width: 100%;
    }
}