.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.photo-gallery {
  display: flex;
  justify-content: center;
  gap: 6.2vw;
  margin: 20px;
}

/* Global Typography Styles */
body {
  font-family: 'Helvetica', sans-serif;
  font-size: 16pt; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

h1 {
  font-family: 'Raleway', sans-serif;
  font-size: 30pt;
  font-weight: bold;
}

h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 24pt;
  font-weight: 500; /* Medium weight */
}

h3 {
  font-family: 'Helvetica', sans-serif;
  font-size: 20pt;
  font-weight: bold;
}

p {
  font-family: 'Helvetica', sans-serif;
  font-size: 16pt;
  font-weight: 400;
}

ol {
  font-family: 'Helvetica', sans-serif;
  font-size: 16pt; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

ul {
  font-family: 'Helvetica', sans-serif;
  font-size: 16pt; /* Base font size for body */
  line-height: 1.5;
  color: #333;
}

.subtext {
  font-family: 'Helvetica', sans-serif;
  font-size: 12pt;
  font-weight: 300; /* Light */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
