/* src/components/HomePage/History.css */
.history-section {
  position: relative;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  padding-top: 50vh;
  background-size: cover; /* Ensure the background image covers the container */
  background-position: center; /* Center the background image */
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.content {
  text-align: center;
  z-index: 1;
}

.content h1 {
  font-family: 'Raleway', sans-serif;
  font-size: 20pt;
}

.content p {
  font-family: 'Raleway', sans-serif;
  font-size: 16pt;
}

.link-button {
  background-color: #fff;
  color: #004d40;
  border: 2px solid #004d40;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  font-family: 'Raleway', sans-serif;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.link-button:hover {
  background-color: #466FD1;
  color: white;
}