.image-section {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: visible; /* Allow vector/image to overflow as needed */
}

.image-wrapper {
  display: grid;
  grid-template-rows: auto auto; /* Two rows: image and caption */
  justify-items: center;
  width: auto;  /* Ensure the width of the container adapts to the image's size */
}

.background-vector {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.3);
  width: 100%;
  height: auto;
  z-index: -10;
  pointer-events: none; /* Ensure the vector doesn't interfere with interaction */
}

.staff-photo {
  max-width: 100%;  /* Ensure the image scales properly */
  height: auto;
  border-radius: 10px;
  grid-row: 1;  /* Position the image in the first row */
}

.partner-caption {
  background-color: #F4E04D; 
  padding: 10px 20px;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 100%;  /* Inherit the width of the image */
  grid-row: 2;  /* Position the caption in the second row */
  margin-top: -2vh;
}

@media (max-width: 768px) {
  .image-section {
    flex-direction: column;
    padding: 10px;
  }

  .background-vector {
    transform: translate(-50%, -50%) scale(1);
    width: 100%;
  }

  .partner-caption {
    margin-top: 10px;
    width: 100%;  /* Ensure the caption fits within the container on smaller screens */
  }
}