.footer {
  background-color: #036955;
  color: #fff;
  padding: 20px 40px;
  margin-top: -1vh;
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18 equal columns */
  gap: 20px;
  align-items: start;
}

.footer-top {
  grid-column: 1 / -1; /* Span all 18 columns */
  display: grid;
  grid-template-columns: repeat(18, 1fr); /* 18 equal columns */
  gap: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  align-items: center;
}

.footer-logo {
  grid-column: 2 / 5; /* Align logo between columns 2 and 4 */
  max-width: 15rem;
  justify-self: start;
}

.footer-content {
  grid-column: 5 / 13; /* Adjust the column span for content */
  display: flex;
  flex-direction: column; /* Stack social and footer links vertically */
  align-items: flex-start; /* Align both social and footer links to the left */
  width: auto;
}

.footer-social {
  display: flex;
  flex-direction: row; /* Arrange icons in a row */
  justify-content: flex-start; /* Ensure social icons are aligned to the left */
  align-items: center;
  gap: 2.5rem; /* Space between icons */
  padding: 0;
  margin-bottom: 2rem; /* Space between social links and footer links */
  width: fit-content;
}

.footer-social a {
  margin: 0; /* Remove extra margin */
}

.social-icon {
  width: 2.5rem;
  height: 2.5rem;
  filter: invert(1); /* Ensure icons remain white */
}

.footer-links {
  display: flex;
  flex-direction: row;
  gap: 7.5rem; /* Space between each link */
  font-size: 16pt;
  width: auto; /* Auto width for better flexibility */
  justify-content: flex-start; /* Align links to the left */
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
  font-size: 18pt;
  white-space: nowrap; /* Prevent wrapping */
}

.footer-links a:hover {
  text-decoration: underline;
}

.footer-address {
  grid-column: 15 / 18;
  text-align: left;
  justify-self: end;
  font-size: 16pt;
  text-decoration: none;
  font-style: normal; /* Ensure no italics */
}

.footer-address p {
  margin: 5px 0;
  font-family: 'Raleway', sans-serif;
  font-size: 16pt;
  font-style: normal; /* Ensure no italics on <p> */
}

/* Policies and footer text alignment */
.footer-policies {
  grid-column: 1 / 5; /* Align with the logo columns */
  display: flex;
  flex-direction: row; /* Stack policies vertically */
  align-items: flex-start; /* Align left */
  gap: 8rem; /* Space between each policy */
  margin-bottom: 3rem;
  color: #fff !important;
  font-family: 'Raleway';
}

.footer-policies a {
  color: #fff;
  text-decoration: none;
}

.footer-bottom {
  grid-column: 2 / 12; /* Align with the logo columns */
  display: flex;
  flex-direction: column; /* Stack footer text vertically */
  align-items: flex-start; /* Align left */
  gap: 10px; /* Space between each policy */
  color: #fff;
  font-family: 'Raleway';
  font-size: 16pt;
}

/* Target the footer text based on the exact selector path */
.footer-bottom p {
  grid-column: 2 / 5; /* Align with the logo columns (2-5) */
  display: flex;
  flex-direction: row; /* Ensure the text stays on one line */
  align-items: flex-start; /* Align left */
  font-family: 'Raleway', sans-serif;
  font-size: 16pt;
  white-space: nowrap; /* Ensure the text stays on one line */
  color: #fff; /* Ensure the text is white */
  margin: 0; /* Remove any unnecessary margin */
}

.footer-top .footer-address p:nth-child(6) {
 margin-top: 2rem;
}

/* Media Queries for Mobile Responsiveness */
@media (max-width: 768px) {
  .footer {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .footer-top {
    grid-template-columns: 1fr;
    align-items: center;
  }

  .footer-logo {
    justify-self: center;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .footer-social {
    margin-bottom: 20px;
  }

  .footer-address {
    justify-self: center;
    margin-bottom: 20px;
  }

  .footer-links {
    flex-direction: column;
    gap: 10px;
    font-size: 14pt;
  }

  .footer-policies {
    grid-column: 1 / -1; /* Span entire width on mobile */
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }

  #root > div > div > footer > div.footer-bottom > p {
    grid-column: 1 / -1; /* Span entire width on mobile */
    align-items: center;
  }
}