.partner-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: var(--button-bg-color, #036955); /* Use CSS variable with default value */
    color: var(--button-text-color, white);            /* Use CSS variable with default value */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16pt;
    font-weight: lighter;
    transition: background-color 0.3s ease;
}
  
.partner-button:hover {
    background-color: var(--button-hover-bg-color, #466FD1); /* Use CSS variable with default value */
}