/* src/components/HomePage/HomeCTA.css */
.cta-container {
  position: relative;
  width: 100%;
  height: 55vh; /* Ensure it takes the full viewport height */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 0; /* Adjust padding to fit content */
  background-size: cover; /* Ensure the background image covers the container */
  background-position: center; /* Center the background image */
}

.cta-content {
  position: relative; /* Make sure it's positioned relative to the container */
  z-index: 5; /* Ensure it's on top of the background */
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  max-width: 400px;
  width: 100%;
  margin-top: 0%; /* Adjust to move the content upwards */
}

.cta-content h3 {
  font-family: 'Raleway-Bold', sans-serif;
  font-size: 24px;
  margin: 10px 0;
  color: #036955;
}

.cta-content p {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  color: #333;
  margin: 10px 0;
}

.cta-content button {
  background-color: #036955;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.cta-content button:hover {
  background-color: #466FD1;
}